.BookCard {
  border: 5px double rgb(31, 21, 122);
  width: 400px;
  height: 300px;
  display: inline-block;
  margin: 10px 15px 15px 15px;
  padding: 10px;
}

.BookCard .deleteButton {
  background-color: white;
  border-radius: 5px;
  border: black solid 2px;
  float: right;
  margin: -9px -9px 0px 0px;
  font-weight: bold;
  padding: 0 6px;
  background-color: rgb(219, 219, 219);
}

.BookCard .deleteButton:hover {
  cursor: pointer;
  background-color: rgb(255, 255, 255);
  color: red;
}